[saprun_enter] {
	flex: 1 1 auto;
	display: flex;
	flex-direction: column;
	align-content: center;
	align-items: center;
	justify-content: space-around;
}

[saprun_enter_form_bar_buttons] {
	flex-direction: column;
	align-items: stretch;
}

[saprun_enter_submit] {
	width: auto;
}

[saprun_enter_status] {
	margin: .5rem;
}

[saprun_enter_reset]:not([disabled]) {
	box-shadow: none;
	background: none;
}
